<script>
  import { onMount } from "svelte";
  import {
    jwtoken,
    csrfToken,
    getCsrfToken,
    nickname,
    modify_postid,
    showNavbar,
  } from "./store.js";
  import { navigate } from "svelte-routing";

  import { get } from "svelte/store";

  import Quill from "quill";
  import "quill/dist/quill.snow.css";

  let title = "";
  let content = "";
  let imageUrl = "";
  let quill;
  let category = "free";

  // 이미지 핸들러 커스터마이징
  function imageHandler() {
    const input = document.createElement("input");
    input.setAttribute("type", "file");
    input.setAttribute("accept", "image/*");
    input.click();

    input.onchange = async () => {
      const file = input.files[0];
      if (!file) {
        console.error("파일이 선택되지 않았습니다.");
        return;
      }

      const reader = new FileReader();
      reader.onloadend = async () => {
        const base64Image = reader.result;
        if (!base64Image) {
          console.error("파일을 읽는 도중 문제가 발생했습니다.");
          return;
        }

        const token = get(csrfToken); // Svelte store에서 현재 값을 가져옴
        const checkjwt = get(jwtoken);

        try {
          const response = await fetch("/uploadimage", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              "CSRF-Token": token,
              Authorization: `Bearer ${checkjwt}`,
            },
            body: JSON.stringify({ image: base64Image }),
          });

          if (!response.ok) {
            throw new Error("이미지 업로드 실패");
          }

          const data = await response.json();
          imageUrl = data.url;

          // 이미지 URL을 에디터에 삽입
          const range = quill.getSelection();
          quill.insertEmbed(range.index, "image", imageUrl);
        } catch (error) {
          console.error("Error uploading image:", error);
        }
      };

      reader.readAsDataURL(file);
    };
  }

  onMount(async () => {
    await getCsrfToken();
    await loadModifyPostData();
    $showNavbar = false;

    quill = new Quill("#editor", {
      theme: "snow",
      placeholder: "Compose an epic...",
      modules: {
        toolbar: {
          container: [
            [{ header: [1, 2, false] }],
            ["bold", "italic", "underline"],
            ["image", "code-block"],
          ],
          handlers: {
            image: imageHandler,
          },
        },
      },
    });

    if (content) {
      quill.clipboard.dangerouslyPasteHTML(content);
    }

    quill.on("text-change", () => {
      content = quill.root.innerHTML;
    });
  });

  const handleSubmit = async () => {
    const data = {
      title: title,
      category: category,
      content: content,
      nickname: $nickname,
      postId: $modify_postid,
    };
    const token = get(csrfToken); // Svelte store에서 현재 값을 가져옴
    const checkjwt = get(jwtoken);

    try {
      const response = await fetch("/writepost", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          "CSRF-Token": token,
          Authorization: `Bearer ${checkjwt}`,
        },
        body: JSON.stringify(data),
      });

      // 응답 로깅
      const responseData = await response.json();

      if (response.ok) {
        alert("글을 성공적으로 작성하였습니다");
        // navigate 함수의 올바른 사용 확인
        navigate("/boardlist");
        modify_postid.set("none");
        showNavbar.set(true);
      } else {
        alert(`오류 발생: ${responseData.error}`);
      }
    } catch (error) {
      console.error("네트워크 오류:", error);
      alert(`네트워크 오류: ${error.message}`);
    }
  };
  async function loadModifyPostData() {
    if ($modify_postid != "none") {
      try {
        const url = `/postget?post_id=${$modify_postid}`;

        const response = await fetch(url);
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }

        const loadedData = await response.json();
        title = loadedData.title;
        content = loadedData.content;
        category = loadedData.category;
      } catch (error) {
        alert("에러가 발생하였습니다");
      }
    }
  }

  function cancel() {
    modify_postid.set("none");
    navigate("/boardlist");
    showNavbar.set(true);
  }
</script>

<div class="table-outline main_data">
  <div class="table-head table-title">
    <input type="text" id="title" bind:value={title} />
  </div>
  <div class="table-contents-gray">
    <div class="table-contents-no-line flex">
      <select class="namewidth" bind:value={category}>
        <option value="free">자유</option>
        <option value="trade">거래</option>
        <option value="setting">공략</option>
      </select>
    </div>
  </div>
  <div class="table-contents-white">
    <div class="table-contents-no-line flex">
      <div id="editor"></div>
    </div>
  </div>

  <div class="form-group">
    <button on:click={() => handleSubmit()}>작성하기</button>
    <button on:click={() => cancel()}>취소하기</button>
  </div>
</div>

<style>
  #editor {
    height: 60vh;
  }
  #title {
    margin: 15px;
    width: 100%;
    background-color: inherit;
    color: inherit;
    border: none;
    font-size: 1.5rem;
    padding: 5px;
  }
  .table-contents-gray div {
    background-color: gray;
  }
  .table-contents-white div {
    background-color: white;
    color: black;
  }
</style>
